body{font-family:"Montserrat",Arial,Helvetica,sans-serif;color:#fcfcfc}a{text-decoration:none}li{list-style:none}ul{margin:0;padding:0}h1,h2,h3,h4,h5,h6{margin:0;padding:0}p{margin:0;padding:0}img{display:block;max-width:100%;height:auto}.container{min-width:320px;padding-left:20px;padding-right:20px;margin:auto}@media screen and (min-width: 768px){.container{padding-left:32px;padding-right:32px}}@media screen and (min-width: 1200px){.container{padding-left:16px;padding-right:16px}}.logo{width:60px;height:65px}.header__nav{display:flex}.nav_item{display:flex}